.imagination {
   position: relative;
   padding-bottom: 26.25em;
   overflow: inherit;
   .container {
      padding-top: 7.5em;
      &:after {
         content: '';
         display: block;
         position: absolute;
         width: 12.375em;
         height: 12.5em;
         top: 0;
         right: 22em;
         background: url(../assets/images/pattern.png)repeat;
      }
   }

   h2 {
      max-width: 8.4375em;
      margin-left: 1.875em;
   }
   p {
      position: relative;
      max-width: 24em;
      margin-top: 5em;
      margin-left: 12em;
      padding-bottom: 0.6em;
      background: $snow;
      &:after {
         content: '';
         display: block;
         position: absolute;
         width: 9.9em;
         height: 26.1em;
         top: 0.75em;
         left: -0.5em;
         background: url(../assets/images/pattern.png)repeat;
         z-index: -1;
      }
   }
   .torus {
      position: absolute;
      display: block;
      text-indent: 9999px;
      white-space: nowrap;
      overflow: hidden;
      z-index: 9999;
      &__1 {
         background: url(../assets/images/torus1.png)no-repeat center;
         background-size: 100%;
         top: -3.125em;
         right: -6.25em;
         width: 28.125em;
         height: 27.8125em;
      }
      &__2 {
         background: url(../assets/images/torus2.png)no-repeat center;
         background-size: 100%;
         left: -15.625em;
         bottom: -18.75em;
         width: 40.625em;
         height: 33.125em;
      }
      &__3 {
         background: url(../assets/images/torus3.png)no-repeat center;
         background-size: 100%;
         right: -6.25em;
         bottom: -12.5em;
         width: 37.5em;
         height: 31.25em;
      }
   }
}


@media screen and(max-width: 1000px) {

   .imagination {
      .container {
         &:after {
            display: none;
         }
      }

      h2 {
         margin: 0 auto;
      }
      p {
         &:after {
            display: none;
         }
      }
   }

}

@media screen and(max-width: 820px) {

   .imagination {
      p {
         margin: 0 auto;
         margin-top: 5em;
      }

      .torus {
         &__3 {
            display: none;
         }
      }
   }

}

@media screen and(max-width: 668px) {

   .imagination {
      .torus {
         &__1 {
            display: none;
         }
      }
   }

}
