// colors
$blue: #3A59FF;
$white: #FFFFFF;
$snow: #F2F2F2;
$black: #333333;
$purple: #C8CAFD;
$green: #9CEEB0;

// font
@font-face {
   font-family: "Avenir-next";
   src: url(../assets/fonts/AvenirNext-Regular.ttf);
}
@font-face {
   font-family: "Andale-mono";
   src: url(../assets/fonts/AndaleMono.ttf);
}
$sans-serif: "Avenir", sans-serif;
$monospace: "Andale-mono", monospace;

* {
   padding: 0;
   margin: 0;
   border: none;
   outline: none;
}

li, a {
   text-decoration: none;
   list-style: none;
}
