.video {
   background: $blue;
   color: $snow;
   .container {
      position: relative;
      padding-top: 7.5em;
      padding-left: 10px;
      padding-right: 10px;
      &:after {
         content: '';
         display: block;
         position: absolute;
         width: 12.375em;
         height: 12.5em;
         top: 0;
         left: 18.125em;
         background: url(../assets/images/patternWhite.png)repeat;
      }
   }

   h2 {
      margin-left: 8.4375em;
      max-width: 6.5625em;
   }
}


#video_player_box {
   position: relative;
   max-width: 72.8125em;
   padding-top: 60%;
   margin: 0 auto;
}
#my_video {
   display: block;
   position: absolute;
   width: calc(100% - 1.5625em);
   height: calc(100% - 1.5625em);
   top: 0;
   right: 0;
}
#video_controls_bar {
   position: absolute;
   width: calc(100% - 1.5625em);
   height: calc(100% - 1.5625em);
   bottom: 0;
   left: 0;
   background: rgba($blue, .5);
   border: 2px solid $snow;
   transition: all .2s ease-in-out;
   &:hover {
      background: rgba($blue, 0);
   }
   &.controls__play {
      background: rgba($blue, 0);
   }
}
.wrap__controls {
   position: absolute;
   left: 0;
   bottom: 0;
   width: calc(100% - 1.875em);
   height: 9.074074074074074%;
   padding: 0.9375em;

   #playpausebtn {
      display: block;
      width: 6%;
      height: 100%;
      text-indent: 9999px;
      white-space: nowrap;
      overflow: hidden;
      background: url(../assets/video/play.png)no-repeat center;
      background-size: 90%;
      float: left;
      margin-right: 0.9375em;
   }
   #volumeslider {
      width: 5%;
      margin-right: 0.9375em;
      margin-top: 2%;
   }
   #seekslider {
      margin-right: 0.9375em;
      width: 37%;
      margin-top: 2%;
   }
   .spantime {
      display: block;
      float: left;
      margin-top: 2%;
      font-family: $monospace;
   }
   #durtimetext {
      margin-right: 0.9375em;
   }
   #fullscreenbtn {
      display: block;
      width: 6%;
      height: 100%;
      text-indent: 9999px;
      white-space: nowrap;
      overflow: hidden;
      background: url(../assets/video/full.png)no-repeat center;
      background-size: 90%;
      float: left;
      margin-right: 0.9375em;
   }
}

@media screen and(max-width: 1000px) {

   .video {
      .container {
         &:after {
            display: none;
         }
      }

      h2 {
         margin: 0 auto;
      }
   }

}


//RANGE
input[type=range] {
   display: block;
   float: left;
  -webkit-appearance: none;
  margin: 10px 0;
}
input[type=range]:focus {
  outline: none;
}
input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 16px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: none;
  background: $blue;
  border-radius: 2px;
  border: 2px solid $snow;
}
input[type=range]::-webkit-slider-thumb {
  box-shadow: none;
  border: 2px solid $snow;
  height: 16px;
  width: 14px;
  border-radius: 2px;
  background: $blue;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: 0;
}
input[type=range]:focus::-webkit-slider-runnable-track {
  background: $blue;
}
input[type=range]::-moz-range-track {
  width: 100%;
  height: 16px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: none;
  background: $snow;
  border-radius: 2px;
  border: none;
}
input[type=range]::-moz-range-thumb {
  box-shadow: none;
  border: none;
  height: 16px;
  width: 14px;
  border-radius: 2px;
  background: $snow;
  cursor: pointer;
}
input[type=range]::-ms-track {
  width: 100%;
  height: 16px;
  cursor: pointer;
  animate: 0.2s;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
input[type=range]::-ms-fill-lower {
  background: $snow;
  border: 2px solid $snow;
  border-radius: 4px;
  box-shadow: none;
}
input[type=range]::-ms-fill-upper {
  background: $snow;
  border: 2px solid $snow;
  border-radius: 4px;
  box-shadow: none;
}
input[type=range]::-ms-thumb {
  box-shadow: none;
  border: none;
  height: 16px;
  width: 14px;
  border-radius: 2px;
  background: $snow;
  cursor: pointer;
}
input[type=range]:focus::-ms-fill-lower {
  background: $snow;
}
input[type=range]:focus::-ms-fill-upper {
  background: $snow;
}
