.object {
   position: relative;
   padding-bottom: 22.5em;
   overflow: inherit;
   .container {
      position: relative;
      padding-top: 7.5em;
      &:after {
         content: '';
         display: block;
         position: absolute;
         width: 12.375em;
         height: 12.5em;
         top: 0;
         left: 3.125em;
         background: url(../assets/images/pattern.png)repeat;
      }
   }

   .bg__purple {
      position: absolute;
      max-width: 52.5em;
      height: 40em;
      right: 0;
      top: 14.375em;
      background: $purple;
      text-indent: 9999px;
      white-space: nowrap;
      overflow: hidden;
      z-index: -1;
   }

   h2 {
      margin-left: 4.6875em;
      max-width: 7.5em;
   }
   p {
      position: relative;
      max-width: 21em;
      margin-left: 15em;
      margin-top: 5em;
      padding-bottom: 0.5em;
      &:before {
         content: '';
         display: block;
         position: absolute;
         width: 9.9em;
         height: 100%;
         background: $snow;
         z-index: -2;
      }
      &:after {
         content: '';
         display: block;
         position: absolute;
         width: 9.9em;
         height: 26.5em;
         top: 0.75em;
         left: -0.5em;
         background: url(../assets/images/pattern.png)repeat;
         z-index: -3;
      }
   }

   .cube {
      position: absolute;
      display: block;
      text-indent: 9999px;
      white-space: nowrap;
      overflow: hidden;
      z-index: 9999;
      &__1 {
         background: url(../assets/images/cube1.png)no-repeat center;
         background-size: 100%;
         top: -3.125em;
         right: -9.375em;
         width: 39.375em;
         height: 39.375em;
      }
      &__2 {
         background: url(../assets/images/cube2.png)no-repeat center;
         background-size: 100%;
         left: -18.75em;
         bottom: 0;
         width: 32.1875em;
         height: 32.1875em;
      }
      &__3 {
         background: url(../assets/images/cube3.png)no-repeat center;
         background-size: 100%;
         right: 0;
         bottom: -23.125em;
         width: 37.5em;
         height: 37.5em;
      }
   }
}


@media screen and(max-width: 1000px) {

   .object {
      .container {
         &:after {
            display: none;
         }
      }

      .bg__purple {
         margin-right: 3.75em;
      }

      h2 {
         margin: 0 auto;
      }
      p {
         &:before {
            display: none;
         }
         &:after {
            display: none;
         }
      }

      .cube {
         position: absolute;
         display: block;
         text-indent: 9999px;
         white-space: nowrap;
         overflow: hidden;
         z-index: 9999;
         &__1 {
            background: url(../assets/images/cube1.png)no-repeat center;
            background-size: 100%;
            top: -3.125em;
            right: -9.375em;
            width: 39.375em;
            height: 39.375em;
         }
         &__2 {
            background: url(../assets/images/cube2.png)no-repeat center;
            background-size: 100%;
            left: -18.75em;
            bottom: 0;
            width: 32.1875em;
            height: 32.1875em;
         }
         &__3 {
            background: url(../assets/images/cube3.png)no-repeat center;
            background-size: 100%;
            right: 0;
            bottom: -23.125em;
            width: 37.5em;
            height: 37.5em;
         }
      }
   }

}

@media screen and(max-width: 820px) {

   .object {
      p {
         margin: 0 auto;
         margin-top: 5em;
      }

      .cube {
         &__1 {
            display: none;
         }
      }
   }

}

@media screen and(max-width: 602px) {

   .object {
      .cube {
         &__2 {
            display: none;
         }
      }
   }

}
