.use {
   background: $green;
   position: relative;
   padding-bottom: 24.6875em;
   overflow: inherit;
   z-index: -5;
   .drap__bg {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      text-indent: 99999px;
      white-space: nowrap;
      overflow: hidden;
      background: url(../assets/images/drap.png)no-repeat center;
      background-size: cover;
   }
   .container {
      padding-top: 7.5em;
      &:after {
         content: '';
         display: block;
         position: absolute;
         width: 12.375em;
         height: 12.5em;
         top: 0;
         left: 14.375em;
         background: url(../assets/images/pattern.png)repeat;
      }
   }

   h2 {
      margin-left: 7.5em;
   }
   p {
      position: relative;
      margin-left: 3em;
      margin-top: 5em;
      padding-bottom: 0.5em;
      &:before {
         content: '';
         display: block;
         position: absolute;
         width: 9.9em;
         height: 100%;
         background: $green;
         z-index: -1;
      }
      &:after {
         content: '';
         display: block;
         position: absolute;
         width: 9.9em;
         height: 31.75em;
         top: 0.75em;
         left: -0.5em;
         background: url(../assets/images/pattern.png)repeat;
         z-index: -2;
      }
   }
}


@media screen and(max-width: 1000px) {

   .use {
      .container {
         &:after {
            display: none;
         }
      }

      h2 {
         margin: 0 auto;
         text-align: center;
      }
      p {
         &:before {
            display: none;
         }
         &:after {
            display: none;
         }
      }
   }

}

@media screen and(max-width: 820px) {

   .use {
      p {
         margin: 0 auto;
         margin-top: 5em;
      }
   }

}
