.project {
   background: $blue;
   color: $snow;
   text-align: center;
   padding-bottom: 100px;

   h2 {
      max-width: 7.5em;
      margin: 0 auto;
      margin-bottom: 100px;
      margin-top: 100px;
   }
   .p__plus {
      max-width: 100%;
      margin-top: 2.777777777777778em;
   }
}

.wrap__label {
   max-width: 48.75em;
   margin: 0 auto;
   margin-bottom: 60px;
}
.content__label {
   margin: 30px 0;
   text-align: left;

   &:first-child {
      margin-top: 0;
   }
   &:last-child {
      margin-bottom: 0;
   }

   p {
      font-family: $monospace;
      font-size: 32px;
   }
   .label {
      font-family: $sans-serif;
      font-size: 24px;
   }
}
