header {
   padding-top: 1.25em;
   padding-bottom: 18.75em;
   .container {
      position: relative;
   }

   .calltoaction {
      position: absolute;
      left: 50%;
      bottom: -9.375em;
      transform: translateX(-50%) translateY(50%);
   }
}
.content__text {
   max-width: 26.25em;
   margin-top: 2.5em;
   margin-left: 3.75em;
   h1 {
      height: 3.359375em;
      margin-bottom: 2.03125em;
   }

   .p__plus {
      position: relative;
      &:before {
         content: '';
         display: block;
         position: absolute;
         top: 0;
         left: 0;
         width: 5.5em;
         height: 100%;
         padding-bottom: 10px;
         background: $snow;
         z-index: -1;
      }
      &:after {
         content: '';
         display: block;
         position: absolute;
         width: 5.5em;
         height: calc(100% + 290px);
         top: 0.416666666666667em;
         left: -0.277777777777778em;
         background: url(../assets/images/pattern.png)repeat;
         z-index: -2;
      }
   }
}

.content__sphere {
   position: absolute;
   top: 0;
   right: 7.5em;
   background: $green;
   width: 48.75em;
   height: 37.5em;
   z-index: -9;
   box-shadow: 0 2px 4px $green;
   img {
      position: relative;
      right: -6.25em;
      top: -2.5em;
      width: 95%;
      animation: bounce 2s ease-in-out alternate infinite;
   }
}

@keyframes bounce {
   0% {
      transform: translateX(0%) translateY(1%);
   }
   100% {
      transform: translateX(.5%) translateY(-1%);
   }
}

@media screen and(max-width: 1000px)  {

.content__text {
   margin: 0 auto;
   h1 {
      margin-bottom: 2.03125em;
   }

   .p__plus {
      &:before {
         display: none;
      }
      &:after {
         display: none;
      }
   }
}
.content__sphere {
   position: relative;
   top: auto;
   right: auto;
   background: $green;
   width: 48.75em;
   max-width: 48.75em;
   margin: 0 auto;
   margin-top: 50px;
   img {
      position: relative;
      right: -6.25em;
      top: -2.5em;
      width: 95%;
   }
}

}


@media screen and(max-width: 1000px) {

header {
   .calltoaction {
      text-align: center;
   }
}

}

@media screen and(max-width: 634px) {

   .content__sphere {
      position: relative;
      top: auto;
      right: auto;
      background: $green;
      width: 100%;
      height: 100%;
      max-width: 48.75em;
      max-height: 37.5em;
      margin: 0 auto;
      margin-top: 50px;
      img {
         position: relative;
         right: auto;
         left: 0;
         width: 95%;
      }
   }

}
