.what {
   position: relative;
   background: $blue;
   color: $snow;
   z-index: -2;
   padding-bottom: 15.625em;
   .container {
      padding-top: 7.5em;
      &:after {
         content: '';
         display: block;
         position: absolute;
         width: 12.375em;
         height: 12.5em;
         top: 0;
         left: 3.125em;
         background: url(../assets/images/patternWhite.png)repeat;
      }
   }
   h2 {
      margin-left: 4.6875em;
      max-width: 6.5625em;
   }
   p {
      position: relative;
      display: inline-block;
      margin-top: 5em;
      margin-right: 3em;
      padding-bottom: 0.6em;
      float: right;
      background: $blue;
      &:after {
         content: '';
         display: block;
         position: absolute;
         width: 9.9em;
         height: 33.75em;
         top: 0.75em;
         left: -0.5em;
         background: url(../assets/images/patternWhite.png)repeat;
         z-index: -1;
      }
   }
   .yumi3d {
      display: block;
      position: absolute;
      width: 75em;
      height: 39.375em;
      transform: translateX(-45%);
      bottom: -6.25em;
      background: url(../assets/images/yumi.png)no-repeat center;
      background-size: 100%;
      text-indent: 9999px;
      white-space: nowrap;
      overflow: hidden;
   }
}

@media screen and(max-width: 1000px) {

   .what {
      padding-bottom: 0;
      .container {
         &:after {
            display: none;
         }
      }
      h2 {
         margin: 0 auto;
         text-align: center;
      }
      p {
         &:after {
            display: none;
         }
      }
      .yumi3d {
         position: relative;
      }
   }

}

@media screen and(max-width: 820px) {

   .what {
      p {
         display: block;
         margin: 0 auto;
         margin-top: 5em;
         float: none;
      }
   }

}
