body {
   background: $snow;
   color: $black;
   font-family: $sans-serif;
   font-size: 16px;
   overflow-x: hidden;
}

section {
   overflow: hidden;
}

.container {
   position: relative;
   max-width: 78.75em;
   margin: 0 auto;
}

h1 {
   display: block;
   width: 100%;
   font-family: $monospace;
   font-size: 4em;
   line-height: 1.625;
   text-indent: 99999px;
   overflow: hidden;
   background: url(../assets/images/logoB.svg)no-repeat center;
   background-size: 100%;
}

h2 {
   font-family: $monospace;
   font-size: 4em;
   font-weight: normal;
   line-height: 1.625;
}

.p__plus {
   font-family: $monospace;
   font-size: 2.25em;
   font-weight: normal;
   line-height: 1.618055555555556;
   letter-spacing: 0.138888888888889em;
}

p {
   font-family: $sans-serif;
   font-size: 1.25em;
   font-weight: normal;
   line-height: 1.75;
   max-width: 24em;
}

.calltoaction {
   display: inline-block;
   padding: 0.6875em 1.5625em;
   padding-top: 0.8875em;
   font-family: $monospace;
   font-size: 2em;
   line-height: 1.4;
   letter-spacing: 0.15625em;
   text-transform: uppercase;
   background: $blue;
   color: $snow;
   box-shadow: 0 4px 14px rgba(6, 30, 156, .66);
   border: none;
   outline: none;
   cursor: pointer;
   z-index: 9999;
   &:hover {
      box-shadow: 0 2px 12px rgba(6, 30, 156, .2);
      transition: all .2s ease-in-out;
   }
}

@media screen and(max-width: 1260px)  {

body {
   font-size: 14px;
}

}
@media screen and(max-width: 1154px)  {

body {
   font-size: 12px;
}

}

@media screen and(max-width: 634px) {

.container {
   padding-left: 10px;
   padding-right: 10px;
}

}
